.banner{
  
  height: 100vh;
  max-height: 500px;
  background: 
//    linear-gradient(to right, $lg 0%, $lg2 100%),
    img('hero-bg.jpg') center / cover no-repeat fixed;
  color: #DFDFE1;//$color-text-light;
  clip-path: polygon(0 0, 100% 0, 100% #{$cpy}, 50% 100%, 0 #{$cpy});
  &__container{
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    height: 100%;
  }
  &__logo{
    max-height: 40%; 
    margin-bottom: 1em;
  }
  &__title{
    text-align: center;
    font-weight: var(--fBold);
    font-family: var(--font-banner);
//    text-transform: uppercase;
    text-shadow: texto3d(3,darken($color-accent,15));
  }
}