.portfolio{

  &__container{
    @include grid('.portfolio__item', 1 2 3, $gutter*2)
  }
}

.portfolio__item{
  border: 1px solid #ddd;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 2px 4px #d0d0d0;
  overflow: hidden;
  border-radius: .3rem;
  img{
    width: 100%;
  }
  //justify-content: space-between;
}
.portfolio__text{
  border-top: 1px solid #ddd;;
  padding: $gutter;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.btn-link{
  @include btn($color-accent, transparent,.8)
}