.main-menu{
  @include edMenu($bp);
  position: absolute;
  top: var(--header-heigth);
  left: 0;
  color: var(--color-text-light);
  background: rgba($color-primary, 1);
  overflow: hidden;
  transition: max-height .3s linear;
  max-height: 0;
  &.show{
    max-height: 12em;
  }
  @include from($bp){
    position: static;
    background: transparent;
    max-height: 3em;
  }
  li{
    border-bottom: 1px solid rgba(aliceblue,.2);
    &:last-child{
      border: none;
    }
    @include from($bp){
      border: none;
    }
//    &:first-child{
//      border-top: 2px solid rgba(aliceblue,.2)
//    }
  }
  a{
    
  }
}