
// alineación de íconos
[class*="icon"].space::before {
  position : relative;
  top : .1em;
  margin-right : .3em;
}

[class*="icon"]::before {
  position : relative;
  top : .25em;
//  margin-right : .3em;
}

// re-escribe el contenido de titulo del menu de documentacion en responsive
.vertical-menu-toggle::after{
  content: "Documentación"
}

// hack ritmo vertical

.section-title {
  margin-top : 0;
}

.margin-top{
  margin-top: calc(var(--header-heigth) + 1em);
}
.padding-top{
  padding-top: calc(var(--header-heigth) + 3em);
}