

:root {
  --font-title: 'Josefin Sans', sans-serif;
  --font-main:  'Montserrat', sans-serif;
  --font-banner: 'Orbitron', sans-serif;
  
  --flight: 300;
  --fNormal: 400;
  --fBold: 700;
  
  --font-size: 1.1rem;
  --font-lh: 1.5rem;

  @media screen and(min-width: 1024px){
    --font-size: 1rem;
    --font-lh: 1.4rem;
  }
  
}

.fs-xsmall{
  font-size: calc(var(--font-size) * .7);
  line-height: calc(var(--font-lh) * .7);
}

.fs-small{
  font-size: calc(var(--font-size) * .9);
  line-height: calc(var(--font-lh) * .9);
}


.fs-big{
  font-size: calc(var(--font-size) * 1.5);
  line-height: calc(var(--font-lh) * 1.5);
}

.fs-xbig{
  font-size: calc(var(--font-size) * 2);
  line-height: calc(var(--font-lh) * 2);
}

body{
  font-size: var(--font-size) ;
  line-height: var(--font-lh);
  font-family: var(--font-main);
  font-weight: var(--fNormal)
}

h1,h2,h3,h4,h5,h6{
  font-weight: var(--fBold);
  font-family: var(--font-title);
}

h1{
  --font-size: 2rem;
  --font-lh: 2.2rem;
}

h2{
  --font-size: 1.8rem;
  --font-lh: 2rem;
}

h3{
  --font-size: 1.6rem;
  --font-lh: 1.8rem;
}

h4{
  --font-size: 1.4rem;
  --font-lh: 1.6rem;
}

h5{
  --font-size: 1.4rem;
  --font-lh: 1.6rem;
}


