@mixin btn($color, $bg,$size:1){
  text-transform: uppercase;
  color: $color;
  background: $bg;
  background: none;
  border: 1px solid;
  padding: 0 1em;
  border-radius: .3rem;
  transition: all .3s;
  cursor: pointer;
  line-height: 2.5em;
  transform: scale($size);
  &:active{
    transform: scale(calc(#{$size}*.8));
    outline: none;
  }
  &:hover {
    background: $color;
    color: #FFFFFF;
    text-decoration: none;
  }
}