:root{
  --g: 0;
  --r: 0;
  --b: 0;
  --a: 0;
  --cpa: hsla(197°, 74%, 10%, .2);
//  --color-primary: rgb(var(--r), var(--g), var(--b)) ;
//  --color-primary: #07232E;
  --color-secundary: #ddd;
  --color-tertiary: red;
  --color-accent: #DD3D03;
  --color-bg: #FAFDFF;
  --color-text: #444;
  --color-text-light: aliceblue;
  --color-text-dark:#111;
  --color-border: #ddd;
  --whatsapp: #25d366;
  --facebook: #3b5998;
  --twitter: #00aced;
  --instagram: #C32AA3;
  --youtube: #FF0000;  
}

$color-primary: lighten(#07232E,5);
$color-secundary: #ddd;
$color-tertiary: red;
$color-accent: #DD3D03;
$color-bg: #FAFDFF;
$color-text: #444;
$color-text-light: aliceblue;
$color-text-dark:#111;
$color-border: #ddd;
$whatsapp: #25d366;
$facebook: #3b5998;
$twitter: #00aced;
$instagram: #C32AA3;
$youtube: #FF0000;  

$banner-color: darken($color-primary,0);
$lg: rgba($banner-color,.75);
$lg2: rgba($banner-color,.75);
$cpy: 94%;

body{
  background: var(--color-bg);
  color: var(--color-text)
}


@function color($color,$opacity){
  @return rgba($color,$opacity)
}

