.main-header{
//  background: var(--color-primary);
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background: rgba($color-primary,.3);
  padding: 0 1em 1em;
  z-index: var(--z-fixed);
//  --header-heigth: 5em;
  height: var(--header-heigth);
//  position: relative;
  border-bottom: 1px solid rgba($color-accent,.85);
  transition: .3s all;
  box-shadow: 0 0px 10px rgba(#ddd,.5);
  & > *{
    transition: .3s all;
  }
  &.fixed{
    background: rgba($color-primary, 1);
    --header-heigth: 5rem;
  }
  &__container{
    max-width: $max-width;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 80% 1fr;
    @include from($bp){
      grid-template-columns: 300px 1fr;

    }
  }
  &__logo{
    position: relative;
    height: var(--header-heigth);
    display: flex;
    align-items: center;
    transition: .3s all;
    a{
      display: flex;
      align-items: center;
      width: calc(100% - 1em);
//      display: block;
      height: 100%;
      transition: .3s all;
      max-height: calc(var(--header-heigth) * .8);
      @include from(m){
        width: 100%;
      }

    }
    span{
      display: none;
    }
    img{
//      max-width: 90%;
      max-height: calc(var(--header-heigth) * .8);
    }
  }
  &__menu{
    height: var(--header-heigth);
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}

.toogle-menu{
  @include showTo($bp);
  $size: 2rem;
  width: $size;
  height: $size * .75;
  background: linear-gradient(var(--color-text-light) 15%, transparent 15%, transparent 42.5%, var(--color-text-light) 42.5%, var(--color-text-light) 57.5%, transparent 57.5%, transparent 85%, var(--color-text-light) 85%);
}
