.totop{
  position: fixed;
  bottom: 1em;
  right: 1em;
  border: 1px solid $color-bg;
  background: $color-accent;
  $size: 2em;
  height: $size;
  width: $size;
  font-size: 1.2em;
  display: none;
  justify-content: center;
  align-items: center;
  color: $color-text-light;
  border-radius: .3rem;
  opacity: 0;
  transition: opacity .3s;
  &.icon-up::before{
    position: relative;
    top: 0;
  }
  
  &.show{
    display: flex;
    opacity: .75;
    &:hover{
      text-decoration: none;
      opacity: 1;
    }
  }
}