}

.social-networks .facebook {
  background: var(--facebook);
}

.social-networks .twitter {
  background: var(--twitter);
}

.form-footer {
  display: grid;
  grid-template-columns: 1fr 88.3px;
  grid-gap: .5rem;
}

.form-footer .loader-mail {
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
}

.form-footer .loader-mail.active {
  opacity: 1;
}

.send {
  color: #fff;
  border: 1px solid;
  margin: 0 !important;
  line-height: 2.8em;
  font-size: .8em;
  padding: 0;
  width: 100%;
  display: block;
  background: none;
  text-align: center;
  text-transform: uppercase;
  border-radius: .3rem;
  transition: all .1s;
  cursor: pointer;
}

.send:active {
  transform: scale(0.95);
  outline: none;
}

.send:hover {
  background: rgba(0, 0, 0, 0.4);
}

.send:focus {
  outline: none;
}

.send.disabled {
  background: #ccc;
  color: #000;
  opacity: .3;
  padding: 0;
  cursor: default;
}

.input__container {
  position: relative;
  width: 100%;
  height: auto;
}

.success-message {
  border-radius: .3rem;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 1.3em);
  background: rgba(255, 255, 255, 0.85);
  transform: scale(0);
  transition: all 200ms ease;
}

.success-message .inner {
  width: 30%;
  margin: 0 auto;
  position: absolute;
  top: 8%;
  left: 50%;
  transform: translate(-50%, 0);
}

.success-message .inner .icon-checkmark circle {
  fill: #3DC480;
  transform-origin: 50% 50%;
  transform: scale(0);
  transition: transform 200ms cubic-bezier(0.22, 0.96, 0.38, 0.98);
}

.success-message .inner .icon-checkmark path {
  transition: stroke-dashoffset 350ms ease;
  transition-delay: 100ms;
}

.success-message.active {
  opacity: 1;
  transform: scale(1);
}

.success-message.active .icon-checkmark circle {
  transform: scale(1);
}

.success-message.active .success-message--text {
  transform: scale(1);
}

.messages-form {
  position: relative;
}

.success-message-close {
  display: block;
  line-height: 2rem;
  width: 2rem;
  text-align: center;
  position: absolute;
  right: .5em;
  top: .5em;
  z-index: var(--z-modal);
  cursor: pointer;
}

.success-message--text {
  position: absolute;
  bottom: 1em;
  text-align: center;
  padding: 0 1.5rem;
  left: 0;
  padding-top: .5em;
  color: #3DC480;
  font-weight: 700;
  transform-origin: 50% 50%;
  transform: scale(0);
  transition: transform 200ms cubic-bezier(0.22, 0.96, 0.38, 0.98);
}

.main-footer {
  padding: 2rem 0 1rem;
}

@media screen and (min-width: 1024px) {
  .main-footer {
    padding: 5rem 0 1.5rem;
  }
}

.main-footer p {
  text-align: center;
  font-weight: 700;
  color: #0a3444;
}

.main-footer p span {
  margin: 0 .3em;
}

.totop {
  position: fixed;
  bottom: 1em;
  right: 1em;
  border: 1px solid #FAFDFF;
  background: #DD3D03;
  height: 2em;
  width: 2em;
  font-size: 1.2em;
  display: none;
  justify-content: center;
  align-items: center;
  color: aliceblue;
  border-radius: .3rem;
  opacity: 0;
  transition: opacity .3s;
}

.totop.icon-up::before {
  position: relative;
  top: 0;
}

.totop.show {
  display: flex;
  opacity: .75;
}

.totop.show:hover {
  text-decoration: none;
  opacity: 1;
}

[class*="icon"].space::before {
  position: relative;
  top: .1em;
  margin-right: .3em;
}

[class*="icon"]::before {
  position: relative;
  top: .25em;
}

.vertical-menu-toggle::after {
  content: "Documentación";
}

.section-title {
  margin-top: 0;
}

.margin-top {
  margin-top: calc(var(--header-heigth) + 1em);
}

.padding-top {
  padding-top: calc(var(--header-heigth) + 3em);
}
