*,
*::before,
*:after {
  box-sizing: border-box;
}

html, body {
//  font-size: fontSize(small);
  margin: 0;
  font-size: 16px;
}

img{
  display: inline-block;
  max-width: 100%;
  height: auto;    
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  line-height: var(--font-lh);
  font-size: var(--font-size);
}

p {
  &:last-child, &:nth-child(only){
    margin-bottom: .5em;
  }
}

a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
}

