.copy, .company__item{
  margin-bottom: 2rem;
  & > ul{
    margin-top: .5rem;
//    padding: 0;
  }
}
.company__item p{
  margin-left: 1.1em;
  margin-right: 1em;
}

.features{
  width: 70%;
  max-width: 1100px;
  margin: 0 auto;
  margin-top: 2rem;
  display: none;
  @include from(m){
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1em;
  }
  &__item{
    background: #fff;
    border: 1px solid #ddd;
    margin-bottom: $gutter;
    padding: $gutter;
    border-radius: .3rem;
  }
  &__img {
    $size: 116px;
    width: $size;
    height: $size;
    margin: 0 auto;
    overflow: hidden;
    background-image: url(../images/script-icon.png);
    background-position: 0 0;
    margin-bottom: 1em;
    &.custom{
      background-position: -116px -232px ;
    }
    &.seo{
      background-position: -348px 0 ;
    }
  }
  &__text{
    text-align: center;
  }
}