.contact{
  $height: 30rem;
  position: relative;
  margin: 0 ;

  //min-height: 30rem;
  //background: yellow;
  &__diagonal{
    position: absolute;
    clip-path: polygon(0 10%, 100% 4%, 100% 90%, 0 96%);
    width: 100%;
    background: $color-accent;
    height: 100%;
    padding: 0;
    top: 0;
    z-index: var(--z-back);
    margin: 0;
    @include from(m){
      max-height: 85%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  &__conteiner{
    @include from(m){
      max-width: 860px;
      margin: 0 auto;
      display: grid;
      grid-template-columns: 50% 1fr;
      grid-gap: 1em;
    }
  }
  &__form{
    background: $color-primary;
    height: 100%;
    padding: 2em 1em;
    border-radius: .3rem;
    color: var(--third-color);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    position: relative;
  }
  &__data{
    padding: 1.2rem 0;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    @include from(m){
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-end;
    }
    p, a{
      text-align: center;
      font-size: var(--font-size-h4);
      font-size: 1.1em ;
      @include from(m){
        font-size: 1.2em ;
        line-height: 1.6em
      }
      &:before{
        position: relative;
        top: 2px;
      }
    }
    a{
      &:hover{
        text-decoration: none;
      }
    }
    .name{
      font-size: 1.4em !important;
    }
    .phone, .email{
      &::before{
        margin-right: .5em;
      }
    }
    .email{
      //padding-bottom: .1em;
      border-bottom: 1px solid transparent;
      &:hover{
        border-bottom: 1px solid;
      }
    }
    .name{
      font-size: var(--font-size-h3);
    }
  }
}

.input-group{
  $padding: .3em;
  $line-height: 2.3em;
  position: relative;
  margin-bottom: $gutter;
  &.error{
    input, textarea{
      outline: 1px solid darkred;
      color: darkred;
    }
    span.error-icon{
      position: absolute;
      right: $padding;
      top: $padding;
    }
  }
  &.noempty{
    label{
      display: none
      //opacity: 0;
    }
  }
  input, label, textarea{
    font-family: var(--font-main);
    font-size: var(--font-size-normal);
  }
  input, label {
    line-height: $line-height;

  }
  input, textarea{
    padding: 0 $padding;
    width: 100%;
    border-radius: .30rem;
    border: 1px solid #ddd;
    &:focus{
      outline: none;
      box-shadow: 0 0 3px 3px rgba(dodgerblue,.4);
    }
  }
  label{
    padding: 0;
    line-height: $line-height;
    position: absolute;
    left: $padding;
    top: 0;
    transition: opacity .3s;
  }
  input{
    max-height: $line-height;
    display: inline-block;
  }
  textarea{
    padding-top: $padding;
    resize: none;
    height: 6rem;
  }
}

.social-networks{
  @extend %lists-style;
  display: flex;
  transform: translateY(1em);
  overflow: hidden;
  border-radius: .3rem;
  a{
    display: block;
    text-align: center;
    line-height: 2.3rem;
    padding: 0 2.3rem;
    transition: background-color .3s;
    &:hover{
      background: rgba(#000,.3);
      text-decoration: none;
    }
  }
  .facebook{
    background: var(--facebook);
  }
  .twitter{
    background: var(--twitter);
  }
}
.form-footer{
//  display: flex;
//  justify-content: flex-end;
//  background: lime;
  display: grid;
  grid-template-columns: 1fr 88.3px;
  grid-gap: .5rem;
  .loader-mail{
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    &.active{
      opacity: 1;
    }
  }

}
.send{
  color: #fff;
  border: 1px solid;
  margin: 0 !important;
  line-height: 2.8em;
  font-size: .8em;
  padding: 0;
  width: 100%;
  display: block;
  background: none;
  text-align: center;
  text-transform: uppercase;
  border-radius: .3rem;
//  transform: scale(.9);
  transition: all .1s;
  cursor: pointer;
//  border: none;
//  background: #000;
  &:active{
    transform: scale(.95);
    outline: none;
  }
  &:hover{
    background: rgba(#000,.4);
  }
  &:focus{
    outline: none;
  }
  &.disabled{
    background: #ccc;
    color: #000;
    opacity: .3;
    padding: 0;
    cursor: default;
  }
}

.input__container{
  position: relative;
  width: 100%;
  height: auto;
}
.success-message {
    border-radius: .3rem;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100% - 1.3em);
    background: rgba(#fff,.85);
    transform: scale(0);
    transition: all 200ms ease;

    .inner {
      width: 30%;
      margin: 0 auto;
      position: absolute;
      top: 8%;
      left:50%;
      transform: translate(-50%, 0);

      .icon-checkmark {
        circle {
          fill: #3DC480;
          transform-origin: 50% 50%;
          transform: scale(0);
          transition: transform 200ms cubic-bezier(.22, .96, .38, .98);
        }
        path {
          transition: stroke-dashoffset 350ms ease;
          transition-delay: 100ms;
        }
      }
    }

    &.active {
      opacity: 1;
      transform: scale(1);
      .icon-checkmark {
        circle {
          transform: scale(1);
        }

      }
      .success-message--text{
        transform: scale(1);
      }
    }
  }
.messages-form{
  position: relative;
}
.success-message-close{
  display: block;
  $size: 2rem;
  line-height: $size;
  width: $size;
  text-align: center;
  position: absolute;
  right: .5em;
  top: .5em;
  z-index: var(--z-modal);
  cursor: pointer;
}
.success-message--text{
  position: absolute;
  bottom: 1em;
  text-align: center;
  padding: 0 1.5rem;
  left: 0;
  padding-top: .5em;
  color: #3DC480;
  font-weight: 700;
  transform-origin: 50% 50%;
  transform: scale(0);
  transition: transform 200ms cubic-bezier(.22, .96, .38, .98);
//  font-size: .9em;
}

