.title-page{
  background: $color-accent;
  color: $color-bg;
  padding: .7rem 0 .5rem;
  text-align: center;
  border-radius: .3rem;
  margin-bottom: 3rem;
  text-shadow: texto3d(2,#333);
  @include from(m){
    display: table;
    padding: .7rem 5.5rem .5rem;
    margin: 0 auto;
    margin-bottom: 2rem;
  }
}

.sub-title{
  color: rgba($color-accent,.75);
  padding-bottom: 0rem;
  border-bottom: 1px solid $color-accent;
  margin-bottom: .5rem;
  line-height: 1;
  @include from(m){
    width: 300px;    
  }
}
.diagonal{

  padding: 5rem 0;
  //background: linear-gradient(to bottom,  #86adcc 0%,#d4e4ef 100%);
  background:
    linear-gradient(to left bottom, $lg 0%, $lg2 100%),
    url(../images/p-bg2.jpg) center center / cover no-repeat fixed;
  margin: 2em 0;
  clip-path: polygon(0 6%, 100% 0, 100% 94%, 0 100%);
  color: #ddd;
  @include to(l){
    clip-path: polygon(0 2%, 100% 0, 100% 98%, 0 100%);
    padding: 3rem 0;
  }
  .title-page{
    color: #ddd;
    &::before, &::after{
      background: #ddd;
    }
  }
}

.msgSendForm{
  $success: lightgreen;
  $error: #f44336; 
  background: #484B57;
  position: fixed;
  z-index: var(--z-modal);
  top: 10%;
  right: 0;
  transform: translateX(100%);
  width: 98%;
  max-width: 450px;
  padding: 1.5em .7em 1em 1em;
  display: grid;
  grid-template-columns: 20% 1fr;
  grid-gap: 1rem;
  color: #fdfdfd;
  transition: transform .3s linear;
  &.show{
    transform: translateX(0);
  }
  &.success{
    color: $success;
  }
  &.error{
    color: $error;
  }
  .close{
    color: #ccc;
    position: absolute;
    right: .4em;
    top: .4em;
    cursor: pointer;
  }
  .status{
    color: inherit;
    font-size: 2em;
    border: 1px solid;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    $size: 60px;
    height: $size;
    width: $size;
//    transform: translateY(.155em);
    position: absolute;
    top: 50%;
    left: 1rem;
    transform: translateY(-50%);
    &:before{
      top: 0;
    }
  }
  p{
    grid-column: 2;
  }
  span{
    display: block;
  }
}
.loader-content {
	margin: 0;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: rgba(black,.95);
    width: 100%;
    position: fixed;
    left: 0;
    top:0;
    z-index: var(--z-modal);
    opacity: 0;
  transform: scale(0);
  transition: all .3s linear;
}
.loader-content.show{
  opacity: 1;
  transform: scale(1);
}

.loader {
    width: 20em;
    height: 20em;
    font-size: 10px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(.7);
}

.loader .face {
    position: absolute;
    border-radius: 50%;
    border-style: solid;
    animation: animate 3s linear infinite;
}

.loader .face:nth-child(1) {
    width: 100%;
    height: 100%;
    color: gold;
    border-color: currentColor transparent transparent currentColor;
    border-width: 0.2em 0.2em 0em 0em;
    --deg: -45deg;
    animation-direction: normal;
}

.loader .face:nth-child(2) {
    width: 70%;
    height: 70%;
    color: lime;
    border-color: currentColor currentColor transparent transparent;
    border-width: 0.2em 0em 0em 0.2em;
    --deg: -135deg;
    animation-direction: reverse;
}

.loader .face .circle {
    position: absolute;
    width: 50%;
    height: 0.1em;
    top: 50%;
    left: 50%;
    background-color: transparent;
    transform: rotate(var(--deg));
    transform-origin: left;
}

.loader .face .circle::before {
    position: absolute;
    top: -0.5em;
    right: -0.5em;
    content: '';
    width: 1em;
    height: 1em;
    background-color: currentColor;
    border-radius: 50%;
    box-shadow: 0 0 2em,
                0 0 4em,
                0 0 6em,
                0 0 8em,
                0 0 10em,
                0 0 0 0.5em rgba(255, 255, 0, 0.1);
}

@keyframes animate {
    to {
        transform: rotate(1turn);
    }
}
