.services{
  & > .title-page {
    
  }
  &__tit{
    background: transparent !important;
  }
  &__container{
    max-width: 960px;
    width: 80%;
    margin: 0 auto;
    @include from(m){
      display: grid;
      grid-template-columns: repeat(3,1fr);
      grid-gap: 2rem;
    }
  }
  &__item{
    background: rgba(#0085CC,.75); //rgba(darken(invert($color-accent),20),.9);
    border-radius: .3rem;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 100px 1fr;
    padding: 0 1em;
    @include to(m){
      margin-bottom: 2rem;
    }
    @include from(m){
      min-height: 400px;      
    }
    color: rgba($color-text-light,.95);
    border: 1px solid rgba(#fff,.5);
  }
  &__title{
    font-size: 1.3rem;
    display: flex;
    align-items: center;
    padding: 0 1rem;
    border-bottom: 1px solid rgba(#fff,.3);
    text-align: center;
  }
  &__list{
    @extend %lists-style;
    margin-top: 1em;
    padding: 0 1em;
    li{
      line-height: 1;
      height: 3em;
//      background: green;
//      margin-bottom: .5em;
//      display: flex;
//      align-items: center;
//      border-bottom: 1px solid rgba(#fff,.3)
    }
  }
}